body {
  margin: 0;
  font-family: 'Futura',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  font-family: "Futura", cursive, sans-serif;
  color: #271c19;
}

body {
  background-color: #55423d;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

/* responsive settings */
.pc_tablet{
  width: 550px;
}

.mobile{
  max-width: 260px;
}

.app {
  background-color: #fff3ec;
  height: min-content;
  margin-top: 30px;
  padding: 35px;
  display: block;
  justify-content: space-evenly;
  line-height: 1.5em;
  border-radius: 10px 10px 0 0;
}

.score-section {
  display: flex;
  font-size: 24px;
  align-items: center;
}

/* question Section */
.question-section {
  width: 100%;
  position: relative;
}

.question-count {
  margin-bottom: 20px;
}

.question-count span {
  font-size: 28px;
}

.question-text {
  margin-bottom: 12px;
}

.timer-text {
  background: rgb(230, 153, 12);
  padding: 15px;
  margin-top: 20px;
  margin-right: 20px;
  border: 5px solid rgb(255, 189, 67);
  border-radius: 15px;
  text-align: center;
}

/* answers Section */
.answer-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

button {
  width: 100%;
  font-size: 16px;
  color: #fff;
  background-color: #e78fb3;
  border-radius: 10px;
  display: flex;
  margin: 5px;
  padding: 5px 5px 5px 10px;
  justify-content: flex-start;
  align-items: center;
  border: 2px solid #55423d;
  cursor: pointer;
}

.correct {
  background-color: #2f922f;
}

.incorrect {
  background-color: #ff3333;
}

button:hover {
  background-color: #555e7d;
}

button:focus {
  outline: none;
}

button svg {
  margin-right: 5px;
}

/* footer Section */
.footer {
  margin-top:50px;
  text-align: center;
  background-color: #ffc0ad;
  margin: 0;
  padding: 15px;
  line-height: 1.5rem;
  border-radius: 0 0 10px 10px;
}